header, footer {
  background-color: #3f7cad;
}

.navbar {
  padding-top: 15px;
  padding-bottom: 15px;
  border: 0;
  border-radius: 0;
  margin-bottom: 0;
  font-size: 12px;
  letter-spacing: 5px;
}

.navbar-nav li a:hover {
  color: #d3d3d3 !important;
}

.topsection { font-family: "Open Sans", sans-serif; }
.topsection h2 { text-align: center; }
.topsection h2 { text-align: center; }
.topsection h4 { text-align: center; }
.skillslist { display: block; text-align: center; background: transparent; margin-top: 0px; }
.skillslist ul { list-style: none; text-align: center; padding:0; margin: 0; font-family: "Segoe UI"; }

.middlesection { font-family: "Open Sans", sans-serif; }
.middlesection h2 { text-align: center; padding-top:15px; padding-bottom:15px; letter-spacing: 5px; color: white; background: #3f7cad; }
.middlesection .card {
  padding: 30px;
  position: relative;
  overflow: hidden;
  background: #fff;
  box-shadow: 0 10px 29px 0 rgba(68, 88, 144, 0.1);
  border: 1px solid #fff;
}

.middlesection .card-img-top {
  width: 45%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  object-fit: contain;
  padding-top: 20px;
  padding-bottom: 20px;
}
.middlesection .card-text { font-family: "Segoe UI"; }

.bottomsection { font-family: "Open Sans", sans-serif; text-align: center;}
.bottomsection h2 { text-align: center; padding-top:15px; padding-bottom:15px; letter-spacing: 5px; color: white; background: #3f7cad; }
.bottomsection h3 { text-align: center; padding:5; }
.bottomsection h5 { text-align: center; padding:5; font-style: italic; padding-top:15px; padding-bottom:5px; font-family: "Segoe UI"; }
.bottomsection ul { list-style: none; text-align: center; padding:0; margin: 0; font-family: "Segoe UI"; }
